import React, { ErrorInfo } from 'react';
import './App.css';
import ReactAudioPlayer from 'react-audio-player';
import {BrowserRouter as Router, useLocation} from 'react-router-dom'
import base64 from 'base-64';




function App() {
  return (
    <div className="App">
      <header className="App-header">   
      <Router>
        <AudioPlayer/>        
      </Router>      
      </header>
    </div>
  );
}


function AudioPlayer(){
  const rawLocationSearch = useLocation().search;
  const locationSearch = rawLocationSearch.slice(-(rawLocationSearch.length-1));

  const [errored, setErrored] = React.useState(false);

  if(errored){
    return <h1>Unable to load snippet</h1>
  }

  const onAudioPlayerError = (error: any)=>{
    setErrored(true);
  }
  
  try{
    const query = new URLSearchParams(base64.decode(locationSearch));
    // const convoId = 'convo1';
    const convoId = query.get('convoId');
    const snippetStart = query.get('snippetStart');
    const snippetEnd = query.get('snippetEnd');
      
    const endpoint = 'http://localhost:1234/backend/open/snippets?convoId=' + convoId 
      + '&snippetStart=' + snippetStart + '&snippetEnd=' + snippetEnd;
  
  
    return(<ReactAudioPlayer    
      src={endpoint}
      // src={"http://localhost:1234/backend/open/snippets?convoId=convo1&snippetStart=5&snippetEnd=20"}    
      autoPlay
      controls
      onError={onAudioPlayerError}
    />)
  }
  catch(error){    
    console.log("ERROR: ", error);
    setErrored(true);
    return <h1>Unable to load snippet</h1>
  }
  
}

export default App;
